* {
  box-sizing: border-box;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 30px 15px;
}

.header {
  width: 100%;
  background: linear-gradient(to right, #d0e3ea, #2196f3);
  padding: 10px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
}

.header-wrap {
  height: 60px;
  align-items: center;
  display: flex;
}

.search-form {
  width: 100%;
  display: inline-block;
  position: relative;
}

.search-form__input {
  width: 100%;
  height: 30px;
  border-style: none;
  border-radius: 7px;
  padding-left: 10px;
}

.search-form__wrap {
  width: 300px;
  margin: 0 auto;
}

.search-form__btn {
  cursor: pointer;
  height: 30px;
  background-color: #f1eff1;
  border-style: none;
  border-radius: 7px;
  align-items: center;
  padding: 10px;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 30px;
  display: flex;
}

.thumb {
  height: 200px;
}

.thumb > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.photo-card {
  width: calc(25% - 11.25px);
  object-fit: fill;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 8px rgba(0, 0, 0, .5);
}

.info {
  border-top: 0;
  justify-content: space-between;
  padding: 5px 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  display: flex;
}

.hidden {
  display: none;
}

.load-more {
  cursor: pointer;
  color: #fff;
  width: 200px;
  height: 40px;
  background-color: #2196f3;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 7px;
  padding: 5px 40px;
}

.load-more:hover, .load-more:focus {
  background-color: #1162a3;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.gallery-btn__wrap {
  text-align: center;
}

